body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#content
{
    height: 100%;
    width: 100%;
}
body, html {
  height: 100%;
  margin: 0;
}
.home {
  /* The image used */
  background-image: url(/assets/img/headbg1.jpg);
  

  /* Full height */
  height: 100%; 
  width: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: aliceblue;
}
.home{
  padding: 100px;
}
.home h1{
  margin: 0px;
}
.home img{
  border-radius: 50%;
  border:10px solid white;
  max-width: 100%;
  height: auto;
}
#menu {
	position:fixed;
	top:0px;
	z-index:9999;
	display: none;
}
.about{
  padding: 100px;
 
  
}
.resume{
  padding: 100px;
  background: #2B2B2B;
  color: white;
}
.active{
  background: rgba(0,0,0,0.1);
}
.resume .info {
  font: 20px 'librebaskerville-italic', serif;
  color: #ccc;
  margin-top: 9px;
}
.resume p{
  margin: 0px;
}
.resume h5{
  margin-top: 20px;
}
.sidenavig ul{
  margin-top: 10px;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: white; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #222; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #111; 
}